.sidebar{
    display: flex;
    flex-direction: column;
    flex:0.35
}

.sidebar__header{
    display: flex;
    justify-content: space-between;
    padding: 20px;
    border-right: 1px solid lightgray;
}

.sidebar__headerRight{
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-width: 10vw;
}

.sidebar__headerRight > .MuiSvgIcon-root{
    margin-right: 2vw;
    font-size: 24px !important;
}

.sidebar__search{
    display: flex;
    padding:10px;
    align-items: center;
    background-color: #f6f6f6;
    height: 39px;
}

.sidebar__searchContainer{
    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height: 35px;
    border-radius: 20px;
}

.sidebar__searchContainer>.MuiSvgIcon-root{
    color: grey;
    padding: 10px;
}
.sidebar__searchContainer > input{
    border: none;
    margin-left: 10px;
    outline-width: 0;
}

.sidebar__chats{
    flex: 1;
    background-color: white;
    overflow: auto;
    
}

.sidebar__contacts{
    flex: 1;
    background-color: white;
    overflow: auto;
}

#room__name{
        width: 80%;
        margin: auto;
        border: none;
        outline-width: 0;
    }
#name{
        text-align: center;
        color: green;
}
#submit__room{
    border: none;
    cursor: pointer;
    background-color: green;
    color: white;
    width: 80%;
    margin: auto;
    border-radius: 11%;
}